img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

ul,
li {
  list-style: none;
}

.container {
  position: sticky;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  padding: 20px 15px;
  height: 100vh;

  background-color: #e7ecf2;
}
